.btn{
  width: 40px;height: 30px;
  font-size: 20px;
  margin: 10px;
}
.num{
  color: #f00;
  font-weight: bold;
  font-size: 30px;
}
.kully{
  width: 500px;
  height: 300px;
  padding: 10px;
  box-sizing: border-box;
  font-size: 14px;
  margin-top: 30px;
}
.kully > .title{
  line-height: 40px;
}
.kully > .cont{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.kully .btn{
  width: 100px;
  height: 35px;
  line-height: 30px;
  font-size: 20px;
  border-radius: 5px;
  border: 1px solid #ccc;
}
.kully .btn span{
  display: inline-block;
  width: 40px;
  text-align: center;
}
.kully .btn button{
  background: none;
  border: none;
  width: 30px;
  font-size: 20px;
  cursor: pointer;
}
.d_price > span:first-child{
  text-decoration: line-through;
  color: #ccc;
}

.kully .total{
  margin-top: 70px;
  text-align: right;
  font-size: 16px;
}
.total span{
  font-size: 32px;
  font-weight: bold;
}
