@charset "utf-8";

*{margin: 0; padding: 0;}
body{
  font-family: "맑은 고딕", arial, sans-serif;
}
.header{
  background-color: #00f;
  text-align: center;
}
.header > h1{
  color: #fff;
  font-size:30px;
}

main{padding:15px 10px;}
.list01{
  margin:10px 0 15px 30px;
}

main .title3{
  color: rgb(177, 68, 199);
  margin: 14px 0;
}

.list01 li{
  line-height: 34px;
}
.list01 ul, .list01 li ol{
  margin-left: 16px;
  background: #ececec;
}
main .title2{
  color: #f00;
  line-height: 40px;
  margin: 14px 0;
}
main .title4{
  line-height: 50px;
}

.footer{
  text-align: center;
  line-height: 40px;
  background: #333;
}
.footer > address {
  font-style: normal;
  color: #fff;
}
